import React, { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/effect-fade";
import "./heroStyle.css";
import { EffectFade } from 'swiper/modules';
import { Pagination } from 'swiper/modules';


import cover from '../images/cover1.jpg';
import cover2 from '../images/cover2.jpg';
import cover3 from '../images/cover3.jpg';
import { motion as m } from "framer-motion";
import { InView, useInView } from "react-intersection-observer";


export default function Hero() {
    const { ref: myRef1, inView: myElementsVisible } = useInView();
    const { ref: myRef2, inView: myElementsVisible2 } = useInView();
    return (
        <>
            <Swiper
                pagination={true}
                modules={[EffectFade, Pagination]}
                effect={'fade'}
                grabCursor='true'
                loop='true'

                className="mySwiper">
                <SwiperSlide>
                    <div className="hero-slider">
                        <img src={cover} alt="" />
                        <div ref={myRef1} className='hero-layer'>
                            <p className="hero-para">Winter Collection 2023</p>
                            <h1>Be Your Kind Of beauty</h1>
                            <div className="hero-flex">
                                <div className="hero-layer-flex">
                                    <i className="bx bx-spa"></i>
                                    <p>High-end Cosmetics</p>
                                </div>
                                <div className="hero-layer-flex">
                                    <i className="bx bx-droplet"></i>
                                    <p>Vegan Products</p>
                                </div>
                                <div className="hero-layer-flex">
                                    <i className="bx bx-brush"></i>
                                    <p>Express Make-up</p>
                                </div>
                            </div>
                            <button className="hero-btn">Discover Now</button>
                        </div>
                    </div>
                </SwiperSlide>

                <SwiperSlide>
                    <div className="hero-slider">
                        <img src={cover2} alt="" />
                        <div className="hero-layer" initial={{ opacity: 0, translateY: 10 }} animate={{ opacity: 1, translateY: -20 }} transition={{ duration: 1 }}>
                            <p className="hero-para">Winter Collection 2023</p>
                            <h1>Look Amazing Today</h1>
                            <div className="hero-flex">
                                <div className="hero-layer-flex">
                                    <i className="bx bx-spa"></i>
                                    <p>High-end Cosmetics</p>
                                </div>
                                <div className="hero-layer-flex">
                                    <i className="bx bx-droplet"></i>
                                    <p>Vegan Products</p>
                                </div>
                                <div className="hero-layer-flex">
                                    <i className="bx bx-brush"></i>
                                    <p>Express Make-up</p>
                                </div>
                            </div>
                            <button className="hero-btn">Discover Now</button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div className="hero-slider">
                        <img src={cover3} alt="" />
                        <div className="hero-layer" initial={{ opacity: 0, translateY: 10 }} animate={{ opacity: 1, translateY: -20 }} transition={{ duration: 1 }}>
                            <div className="hero-layer-content">
                                <p className="hero-para">Winter Collection 2023</p>
                                <h1>Love Yourself and Face</h1>
                                <div className="hero-flex">
                                    <div className="hero-layer-flex">
                                        <i className="bx bx-spa"></i>
                                        <p>High-end Cosmetics</p>
                                    </div>
                                    <div className="hero-layer-flex">
                                        <i className="bx bx-droplet"></i>
                                        <p>Vegan Products</p>
                                    </div>
                                    <div className="hero-layer-flex">
                                        <i className="bx bx-brush"></i>
                                        <p>Express Make-up</p>
                                    </div>
                                </div>
                                <button className="hero-btn">Discover Now</button>

                            </div>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </>
    );
};