import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import user1 from "../images/user.webp";
import user2 from "../images/user2.webp";
import user3 from "../images/user3.webp";

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';

import './reviewsStyle.css';

// import required modules
import { Pagination } from 'swiper/modules';

export default function Reviews() {
    return (
        <>
            <div className="reviews-row">
                <div className="reviews-heading">
                    <p>Customers Reviews</p>
                    <h2>What Our Clients Say</h2>
                </div>
                <Swiper

                    breakpoints={{
                        450: {
                            width: 450,
                            slidesPerView: 1
                        },
                        680: {
                            width: 530,
                            slidesPerView: 1
                        },
                        850: {
                            width: 700,
                            slidesPerView: 1
                        },
                        1000: {
                            width: 1100,
                            slidesPerView: 2
                        },
                    }}
                    slidesPerView={2}
                    spaceBetween={30}
                    pagination={{
                        clickable: true,
                    }}
                    modules={[Pagination]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <div className="reviews-col">
                            <div className="rating">
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star-half"></i>
                            </div>
                            <p> This is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like
                                Aldus PageMaker including versions of Lorem Ipsum.
                            </p>
                            <div className="reviews-flex">
                                <div className="reviews-img">
                                    <img src={user1} alt="" />
                                </div>
                                <p>Salim Rana / <span className='text-muted'>Web Developer</span></p>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="reviews-col">
                            <div className="rating">
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star-half"></i>
                            </div>
                            <p> This is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like
                                Aldus PageMaker including versions of Lorem Ipsum.
                            </p>
                            <div className="reviews-flex">
                                <div className="reviews-img">
                                    <img src={user2} alt="" />
                                </div>
                                <p>Jake Weary / <span className='text-muted'>CO Owner</span></p>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide>
                        <div className="reviews-col">
                            <div className="rating">
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star"></i>
                                <i className="bx bxs-star-half"></i>
                                <i className="bx bx-star"></i>
                            </div>
                            <p>This is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like
                                Aldus PageMaker including versions of Lorem Ipsum.
                            </p>
                            <div className="reviews-flex">
                                <div className="reviews-img">
                                    <img src={user3} alt="" />
                                </div>
                                <p>Selina Mclarry / <span className='text-muted'>CO Founder</span></p>
                            </div>
                        </div>
                    </SwiperSlide>
                </Swiper>
            </div>
        </>
    );
}