import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import img1 from "../images/cov3.webp";
import './banner2Style.css';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { useRef } from 'react';

import { Navigation } from 'swiper/modules';
import { Products } from '../Data/products';

export default function Banner2() {
    const myRef = useRef();
    return (
        <>
            <div className="banner2-row">
                <div className="banner2-col left-banner">
                    <img className='first-img' src={img1} alt="" />
                    <div className="banner2-layer">
                        <i className='bx bx-plus bx-tada bx-rotate-90' ></i>
                        <i className='bx bx-plus bx-tada bx-rotate-90' ></i>                    </div>
                </div>
                <div className="banner2-col">
                    <div className="banner2-content">
                        <div className="banner2-heading">
                            <p>Trending This Week's</p>
                            <h3>Special Products</h3>
                        </div>
                        <Swiper
                            navigation={false}
                            modules={[Navigation]}
                            onBeforeInit={(swiper) => {
                                myRef.current = swiper;
                            }}
                            slidesPerView={1}
                            spaceBetween={30}
                            className="mySwiper mySwiperBanner"
                        >
                            {
                                Products.map((prod) => (
                                    <SwiperSlide key={prod.id} className='banner-slide'>
                                        <div className="swiper-img">
                                            <img src={prod.img} alt="" />
                                            <div className="banner2-swiper-layer">
                                                <i className="bx bx-shopping-bag"></i>
                                                <i className="bx bx-show"></i>
                                                <i className="bx bx-heart"></i>
                                            </div>
                                            <div className="bottom-layer">
                                                <button>Add To Cart</button>
                                            </div>
                                        </div>

                                        <div className="banner2-text">
                                            <p>{prod.type}</p>
                                            <h3>{prod.title}</h3>
                                            <b>{prod.price}</b>
                                        </div>

                                    </SwiperSlide>
                                ))
                            }
                            <div className="controls">
                                <i onClick={() => myRef.current?.slidePrev()} className="bx bx-chevron-left"></i>
                                <i onClick={() => myRef.current?.slideNext()} className="bx bx-chevron-right"></i>
                            </div>
                        </Swiper>
                    </div>
                </div>
            </div>
        </>
    );
}