import React from 'react';
import './navbarStyle.css';
import logo from '../images/logo.svg';
import logo2 from '../images/logo2.svg';
import cat from "../images/smlCat.webp"
import cat2 from "../images/smlcat2.webp"
import cat3 from "../images/smlcat3.webp"
import cat4 from "../images/smlcat4.webp"
import cartImg from "../images/empty-cart.webp"
import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux/es/hooks/useSelector';
import { useDispatch } from 'react-redux';
import { Add, remove } from '../Redux/features/cartSlice';
import { Link } from 'react-router-dom';
import CartIsFull, { CartIsEmpty } from '../Data/CartInfo';



const Navbar = () => {
    const [menuClicked, setmenuClicked] = useState(false);
    const [dropdownClicked, setdropdownClicked] = useState(false);
    const [cartClicked, setcartClicked] = useState(false);
    const [scrolled, setscrolled] = useState(false);
    const cart = useSelector(state => state.cart.items);
    const dispatch = useDispatch();


    const handleDropdown = () => {
        if (dropdownClicked == false) {
            setdropdownClicked(true);
        } else {
            setdropdownClicked(false);
        }
    }

    const handleCart = () => {
        if (cartClicked == false) {
            setcartClicked(true);
        } else {
            setcartClicked(false);
        }
    }


    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY != "undefined") {
                return window.scrollY;
            }
        }

        window.addEventListener('scroll', () => {
            let scrolled = handleScroll();

            if (scrolled > 85) {
                setscrolled(true);
            }
            else {
                setscrolled(false);
            }
        })
    })

    let total = 0;

    cart.forEach(item => {
        total += item.totalPrice;
    });

    return (
        <div className='navbar-section'>
            <div className={scrolled ? "navbar-row active" : "navbar-row"}>
                <div className="logo">
                    <div className="logo-img">
                        {scrolled ? <img src={logo2} alt="" /> : <img src={logo} alt="" />}
                    </div>
                </div>
                <div className="navlinks">
                    <Link className='link2' to={"/"}>Home </Link>
                    <Link className='link2' to={"/Shop"}>Shop </Link>
                    <Link className='link2' to={"/Cart"}>Cart</Link>
                    <Link className='link2' to={"/Blog"}>Blog</Link>
                    <Link className='link2' to={"/Contact"}>Contact</Link>
                </div>
                <div className="nav-icons">
                    <i className="bx bx-search"></i>
                    <i className="bx bx-heart"></i>
                    <i className="bx bx-shopping-bag cart-icon" onClick={() => { handleCart() }}>
                        <span className={scrolled ? 'cart-counter active' : 'cart-counter'}>{cart.length}</span>
                    </i>
                    <i className="bx bx-menu-alt-right side-menu" onClick={() => { setmenuClicked(true) }}>
                    </i>
                </div>
            </div>
            <div className={menuClicked ? "sidebar active" : "sidebar"}>
                <img src={logo2} className="mt-3 ms-3" />
                <span className='close-btn' onClick={() => setmenuClicked(false)}>&times;</span>
                <div className="sidebar-dropper" onClick={() => { handleDropdown() }}>
                    <div className="dropper-flex">
                        <p><i className="bx bx-menu mx-2"></i>All Categories</p>
                        <i className="bx bx-chevron-down"></i>
                    </div>
                    <div className={dropdownClicked ? "sidebar-dropdown show" : "sidebar-dropdown"}>
                        <div className="dropdown-flex">
                            <div className="dropdown-img">
                                <img src={cat} alt="" />
                            </div>
                            <p>Beauty of Skin</p>
                        </div>
                        <div className="dropdown-flex">
                            <div className="dropdown-img">
                                <img src={cat2} alt="" />
                            </div>
                            <p>Awesome Lip Care</p>
                        </div>
                        <div className="dropdown-flex">
                            <div className="dropdown-img">
                                <img src={cat3} alt="" />
                            </div>
                            <p>Facial Care</p>
                        </div>
                        <div className="dropdown-flex">
                            <div className="dropdown-img">
                                <img src={cat4} alt="" />
                            </div>
                            <p>Discover SkinCare</p>
                        </div>
                    </div>
                </div>
                <div className="sidebar-links">
                    <p>Home</p>
                    <p>Products</p>
                    <p>eCommerce</p>
                    <p>More Pages</p>
                    <p>Coupons</p>
                    <p>Blog</p>
                    <p>Contact</p>
                </div>
                <button className="sidebar-btn">Contact Us</button>
            </div>

            {/***************Shopping Cart**************/}

            <div className={cartClicked ? "cart-section active" : "cart-section"}>
                <div className="cart">
                    <div className="cart-heading">
                        <b>Shopping Cart</b>
                        <span onClick={() => handleCart()}>&times;</span>
                    </div>
                    <CartIsFull>
                        <div className="cart-body">
                            {
                                cart.map((cart) => (
                                    <div key={cart.id} className="cart-flex">
                                        <div className="cart-img">
                                            <img src={cart.img} alt="" />
                                        </div>
                                        <div className="cart-title">
                                            <b>{cart.title}</b>
                                            <p>{cart.price}</p>
                                            <div className="quantity-flex">
                                                <i className="bx bx-minus border-end" onClick={() => { dispatch(remove(cart.id)) }}></i>
                                                <input type="text" defaultValue={cart.quantity} name="" id="" />
                                                <i className="bx bx-plus border-start" onClick={() => { dispatch(Add(cart)) }}></i>
                                            </div>
                                            <span onClick={() => { dispatch(remove(cart.id)) }}>&times;</span>
                                        </div>
                                    </div>
                                ))
                            }
                        </div>
                    </CartIsFull>
                    <CartIsFull>
                        <div className="cart-footer">
                            <div className="cart-flex">
                                <b>Total</b>
                                <b>${total}</b>
                            </div>
                            <div className="cart-buttons">
                                <Link to={'/Cart'} className='btn cart-btn py-3'>View Cart</Link>
                                <button className='btn cart-btn'>Checkout</button>
                            </div>
                        </div>
                    </CartIsFull>
                    <CartIsEmpty>
                        <div className="cart-alt">
                            <div className="cart-alt-img">
                                <img src={cartImg} alt="" />
                            </div>
                            <div className="cart-alt-text">
                                <h3 className='mb-5'>Your Cart is Empty</h3>
                                <button className='btn btn-dark'><a href="/Shop" style={{color: "white" , textDecoration: 'none'}}>Go To Shop</a></button>
                            </div>
                        </div>
                    </CartIsEmpty>
                </div>
            </div>
        </div>
    )
}

export default Navbar;