import React from 'react'
import "./galleryStyle.css"
import img1 from "../images/insta1.webp"
import img2 from "../images/insta2.webp"
import img3 from "../images/insta3.jpg"
import img4 from "../images/insta4.jpg"
import img5 from "../images/insta5.jpg"
import img6 from "../images/insta6.jpg"

const Gallery = () => {
    return (
        <div className='gallery-section'>
            <div className="gallery-row">
                <div className="gallery-col">
                    <img src={img1} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
                <div className="gallery-col">
                    <img src={img2} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
                <div className="gallery-col">
                    <img src={img3} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
                <div className="gallery-col">
                    <img src={img4} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
                <div className="gallery-col">
                    <img src={img5} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
                <div className="gallery-col">
                    <img src={img6} alt="" />
                    <div className="gallery-layer">
                        <i className="bx bxl-instagram"></i>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Gallery;