import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    items: [],
    oneProduct: [],
    totalQuantity: 0,
}

const cartSlice = createSlice({
    name: 'cart',
    initialState,
    reducers: {
        Add(state, action) {
            const newItem = action.payload;

            const existedItem = state.items.find((item) => item.id === newItem.id);

            if (existedItem) {
                existedItem.totalPrice += newItem.price;
                existedItem.quantity++;
            } else {
                state.items.push(newItem);
            }
            state.totalQuantity++;
        },
        remove(state, action) {
            const id = action.payload;
            const matchedItem = state.items.find((item) => item.id === id);
            if (matchedItem.quantity === 1) {
                state.items = state.items.filter((item) => item.id !== id);
                state.totalQuantity--;
            } else {
                matchedItem.quantity--;
                matchedItem.totalPrice -= matchedItem.price;
            }

        },
        addProd(state, action) {
            if (state.oneProduct.length === 1) {
                state.oneProduct.shift();
            } if(state.oneProduct.length === 0) {
                state.oneProduct.push(action.payload);
            }
        }
    }
});

export const { Add, remove, addProd } = cartSlice.actions;

export default cartSlice.reducer;