import React from 'react'
import Heading from '../Constants/heading';
import "./categoryStyle.css";
import { Products } from "../Data/products"
import { useDispatch } from 'react-redux';
import { Add, addProd } from '../Redux/features/cartSlice';
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react';



const Category = () => {
    const dispatch = useDispatch();



    return (
        <div className='category-section'>
            <Heading
                headingPara="Best Sellers in This Week"
                headingH="Enjoy The best quality"
            />

            <div className="category-row">
                {
                    Products.slice(8, 16).map((prod) => (
                        <div  key={prod.id} className="category-col" onClick={() => { dispatch(addProd(prod)) }}>
                            <div className="category-img">
                                <a href='OneProduct' className="direct-link"></a>
                                <img src={prod.img} alt="" />
                                <div className="category-icons">
                                    <i className="bx bx-shopping-bag" onClick={() => dispatch(Add(prod))}></i>
                                    <i className="bx bx-show"></i>
                                    <i className="bx bx-heart"></i>
                                </div>
                                <div className="category-layer">
                                    <button className="" onClick={() => { dispatch(Add(prod)) }}>Add To Cart</button>
                                </div>
                            </div>
                            <div className="category-content">
                                <p>{prod.type}</p>
                                <h3><Link to={"/OneProduct"} className='title-link'>{prod.title}</Link></h3>
                                <b>$ {prod.price}.00</b>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}

export default Category;