import React from 'react'
import Heading from '../Constants/heading';
import "./categoryStyle.css";
import { Products } from "../Data/products"
import { useDispatch } from "react-redux"
import { Add, addProd } from '../Redux/features/cartSlice';
import { UseSelector, useSelector } from 'react-redux/es/hooks/useSelector';
import { Link } from 'react-router-dom';



const Category = () => {
    const cart = useSelector(state => state.cart);


    const dispatch = useDispatch();



    const addToCart = (prod) => {
        dispatch(Add(prod));
    }

    return (
        <div className='category-section'>
            <Heading
                headingPara="Shop by Category"
                headingH="Best Sellers in beauty"
            />

            <div className="category-row">
                {
                    Products.slice(0, 8).map((prod, i) => (
                        <div key={i} className="category-col" onClick={() => { dispatch(addProd(prod)) }} >
                            <div className="category-img">
                            <a href='OneProduct' className="direct-link"></a>
                                <img src={prod.img} alt="" />
                                <div className="category-icons">
                                    <i onClick={() => { addToCart(prod) }} className="bx bx-shopping-bag"></i>
                                    <i className="bx bx-show"></i>
                                    <i className="bx bx-heart"></i>
                                </div>
                                <div className="category-layer">
                                    <button className="" onClick={() => addToCart(prod)}>Add To Cart</button>
                                </div>
                            </div>
                            <div className="category-content">
                                <p>{prod.type}</p>
                                <h3><Link to={'/OneProduct'} className='title-link'>{prod.title}</Link></h3>
                                <b>{prod.price}</b>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}

export default Category;