import React from 'react';
import "./contactStyle.css";
import icon1 from "../../images/contact-icon.webp";
import icon2 from "../../images/contact-icon2.webp";
import icon3 from "../../images/contact-icon3.webp";
import { useEffect, useRef } from 'react'
import Navbar from '../../Components/navbar';
import Footer from '../../Components/footer';
import payment from "../../images/pay.png";
import { useState } from 'react';
import prod1 from "../../images/prod1.webp";
import prod2 from "../../images/prod2.webp";
import prod3 from "../../images/prod3.webp";
import { Products } from '../../Data/products';
import { useDispatch } from 'react-redux';
import { Add, addProd, remove } from '../../Redux/features/cartSlice';
import cartImg from "../../images/empty-cart.webp"
import logo2 from '../../images/logo2.svg';
import cat from "../../images/smlCat.webp"
import cat2 from "../../images/smlcat2.webp"
import cat3 from "../../images/smlcat3.webp"
import cat4 from "../../images/smlcat4.webp"
import prod from "../../images/prod1.webp"
import { useSelector } from 'react-redux/es/hooks/useSelector';
import { Link } from 'react-router-dom';
import CartIsFull, { CartIsEmpty } from '../../Data/CartInfo';
const Contact = () => {
    const [selected, setselected] = useState('');
    const [TextClicked, setTextClicked] = useState(false);
    const [scrolled, setscrolled] = useState(false);
    const [menuClicked, setmenuClicked] = useState(false);
    const [dropdownClicked, setdropdownClicked] = useState(false);
    const [cartClicked, setcartClicked] = useState(false);
    const cart = useSelector(state => state.cart.items);
    const item = useSelector((state) => state.cart.oneProduct)
    const [mainImg, setmainImg] = useState(null);
    const [HalfScrolled, setHalfScrolled] = useState(false);
    const [ToScroll, setToScroll] = useState('')
    const handleDropdown = () => {
        if (dropdownClicked == false) {
            setdropdownClicked(true);
        } else {
            setdropdownClicked(false);
        }
    }

    const handleCart = () => {
        if (cartClicked == false) {
            setcartClicked(true);
        } else {
            setcartClicked(false);
        }
    }



    const ref1 = useRef();


    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY != "undefined") {
                return window.scrollY;
            }
        }

        window.addEventListener('scroll', () => {
            let scrolled = handleScroll();
            let scrolle = scrolled + 'px';
            setToScroll(scrolle);


            if (scrolled > 95) {
                setscrolled(true);
            }
            else {
                setscrolled(false);
            }
        })
    })


    const dispatch = useDispatch();

    const handleText = () => {
        if (TextClicked == false) {
            setTextClicked(true);
        } else {
            setTextClicked(false);
        }
    }


    let total = 0;

    cart.forEach(item => {
        total += item.totalPrice;
    });


    useEffect(() => {
        item.map((prod) => {
            setmainImg(prod.img);
            console.log(mainImg);
        })
    }, [])
    return (
        <div className='contact-section'>

            <div className='cart-navbar'>
                <div className={scrolled ? "navbar-row active" : "navbar-row"}>
                    <div className="logo">
                        <div className="logo-img">
                            {scrolled ? <img src={logo2} alt="" /> : <img src={logo2} alt="" />}
                        </div>
                    </div>
                    <div className="navlinks">
                        <Link className='link' to={"/"}>Home </Link>
                        <Link className='link' to={"/Shop"}>Shop </Link>
                        <Link className='link' to={"/Cart"}>Cart</Link>
                        <Link className='link' to={"/Blog"}>Blog</Link>
                        <Link className='link' to={"/Contact"}>Contact</Link>
                    </div>
                    <div className="nav-icons">
                        <i className="bx bx-search"></i>
                        <i className="bx bx-heart"></i>
                        <i className="bx bx-shopping-bag cart-icon" onClick={() => { handleCart() }}>
                            <span className={scrolled ? 'cart-counter active' : 'cart-counter'}>{cart.length}</span>
                        </i>
                        <i className="bx bx-menu-alt-right side-menu" onClick={() => { setmenuClicked(true) }}>
                        </i>
                    </div>
                </div>
                <div className={menuClicked ? "sidebar active" : "sidebar"}>
                    <img src={logo2} className="mt-3 ms-3" />
                    <span className='close-btn' onClick={() => setmenuClicked(false)}>&times;</span>
                    <div className="sidebar-dropper" onClick={() => { handleDropdown() }}>
                        <div className="dropper-flex">
                            <p><i className="bx bx-menu mx-2"></i>All Categories</p>
                            <i className="bx bx-chevron-down"></i>
                        </div>
                        <div className={dropdownClicked ? "sidebar-dropdown show" : "sidebar-dropdown"}>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat} alt="" />
                                </div>
                                <p>Beauty of Skin</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat2} alt="" />
                                </div>
                                <p>Awesome Lip Care</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat3} alt="" />
                                </div>
                                <p>Facial Care</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat4} alt="" />
                                </div>
                                <p>Discover SkinCare</p>
                            </div>
                        </div>
                    </div>
                    <div className="sidebar-links">
                        <p>Home</p>
                        <p>Products</p>
                        <p>eCommerce</p>
                        <p>More Pages</p>
                        <p>Coupons</p>
                        <p>Blog</p>
                        <p>Contact</p>
                    </div>
                    <button className="sidebar-btn">Contact Us</button>
                </div>

                {/***************Shopping Cart**************/}

                <div className={cartClicked ? "cart-section active" : "cart-section"}>
                    <div className="cart">
                        <div className="cart-heading">
                            <b>Shopping Cart</b>
                            <span onClick={() => handleCart()}>&times;</span>
                        </div>
                        <CartIsFull>
                            <div className="cart-body">
                                {
                                    cart.map((cart) => (
                                        <div key={cart.id} className="cart-flex">
                                            <div className="cart-img">
                                                <img src={cart.img} alt="" />
                                            </div>
                                            <div className="cart-title">
                                                <b>{cart.title}</b>
                                                <p>{cart.price}</p>
                                                <div className="quantity-flex">
                                                    <i className="bx bx-minus border-end" onClick={() => { dispatch(remove(cart.id)) }}></i>
                                                    <input type="text" value={cart.quantity} name="" id="" />
                                                    <i className="bx bx-plus border-start" onClick={() => { dispatch(Add(cart)) }}></i>
                                                </div>
                                                <span onClick={() => { dispatch(remove(cart.id)) }}>&times;</span>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        </CartIsFull>
                        <CartIsFull>
                            <div className="cart-footer">
                                <div className="cart-flex">
                                    <b>Total</b>
                                    <b>${total}</b>
                                </div>
                                <div className="cart-buttons">
                                    <Link to={'/Cart'} className='btn cart-btn py-3'>View Cart</Link>
                                    <button className='btn cart-btn'>Checkout</button>
                                </div>
                            </div>
                        </CartIsFull>
                        <CartIsEmpty>
                            <div className="cart-alt">
                                <div className="cart-alt-img">
                                    <img src={cartImg} alt="" />
                                </div>
                                <div className="cart-alt-text">
                                    <h3 className='mb-5'>Your Cart is Empty</h3>
                                    <button className='btn btn-dark'><a href="/Shop" style={{ color: "white", textDecoration: 'none' }}>Go To Shop</a></button>
                                </div>
                            </div>
                        </CartIsEmpty>

                    </div>
                </div>
            </div>
            <div className="contact-heading text-center">
                <h1>Keep In Touch With Us</h1>
                <div className="contact-heading-flex">
                    <Link to={"/"} className='title-link'><p>Home</p></Link>
                    <i className="bx bx-chevron-right pt-1"></i>
                    <p>Contact</p>
                </div>
            </div>
            <div className="contact-row">
                <div className="contact-col">
                    <h2 className='mb-5'>Send a Message</h2>
                    <form action="">
                        <div className="mb-3">
                            <label htmlFor="" className="form-label">Your Name</label>
                            <input type="text" className='form-control py-3' name="" id="" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="" className="form-label">Your Email</label>
                            <input type="text" className='form-control py-3' name="" id="" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="" className="form-label">Your Subject</label>
                            <input type="text" className='form-control py-3' name="" id="" />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="" className="form-label">Your Message</label>
                            <textarea name="" id="" cols="30" rows="6" className="form-control"></textarea>
                        </div>
                        <input type="checkbox" name="" id="" /> <small className='fs-6 text-muted'>Save my name and email in this browser for next time i comment.</small>
                        <br />
                        <button className="contact-btn btn btn-dark py-2 my-3">
                            Send Message
                        </button>
                    </form>
                </div>
                <div className="contact-col">
                    <div className="contact-info">
                        <div className="contact-col-img">
                            <img src={icon1} alt="" />
                        </div>
                        <div className="contact-text">
                            <p>Contact@shofy.com</p>
                            <b>+670 413 90 762</b>
                        </div>
                    </div>
                    <div className="contact-info">
                        <div className="contact-col-img">
                            <img src={icon2} alt="" />
                        </div>
                        <div className="contact-text">
                            <p>84 Sleepy Holow st. Jamaica, New York 1432</p>
                        </div>
                    </div>
                    <div className="contact-info">
                        <div className="contact-col-img">
                            <img src={icon3} alt="" />
                        </div>
                        <div className="contact-text">
                            <p>Find us on social media</p>
                            <div className="contact-info-icons">
                                <i className="bx bxl-facebook"></i>
                                <i className="bx bxl-linkedin"></i>
                                <i className="bx bxl-twitter"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="map-section">
                <div className="map">
                    <iframe className='map-frame' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d774452.797098001!2d-75.1621712895048!3d40.69145528523254!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c24fa5d33f083b%3A0xc80b8f06e177fe62!2sNew%20York%2C%20NY%2C%20USA!5e0!3m2!1sen!2sma!4v1689269824120!5m2!1sen!2sma" width="1090" height="300" style={{ border: 0 }} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default Contact;