import React from 'react'
import img2 from "../../images/cov1.jpg";
import img1 from "../../images/cov2.jpg";
import "./lgBannerStyle.css";
import { useState, useEffect } from 'react';



const LgBanner = () => {
    const [DaysTimer, setDaysTimer] = useState('00');
    const [HoursTimer, setHoursTimer] = useState('00');
    const [MinutesTimer, setMinutesTimer] = useState('00');
    const [SecondsTimer, setSecondsTimer] = useState('00');





    useEffect(() => {
        const startTimer = () => {
            const targetDate = new Date('August 21, 2023 00:00:00');

            let interval = setInterval(() => {
                const currentDate = new Date().getTime();
                const distance = targetDate - currentDate;

                const days = Math.floor(distance / (1000 * 60 * 60 * 24));
                const hours = Math.floor(distance % (1000 * 60 * 60 * 24) / (1000 * 60 * 60));
                const minutes = Math.floor(distance % (1000 * 60 * 60) / (1000 * 60));
                const seconds = Math.floor(distance % (1000 * 60) / (1000));

                if (distance < 0) {
                    clearInterval(interval.current);
                } else {
                    setDaysTimer(days);
                    setHoursTimer(hours);
                    setMinutesTimer(minutes);
                    setSecondsTimer(seconds);
                }
            }, 1000);
        }
        startTimer();

        // return () => {
        //     clearInterval(interval.current);
        // }
    });

    return (
        <div className='lgbanner-section'>
            {/* <a className='under' href="">Hello World Welcome To <br /> Khalid West Coast Course</a> */}
            <div className="lgbanner-row">
                <div className="lgbanner-col">
                    <div className="lgbanner-img">
                        <img src={img1} alt="" />
                        <div className="lgbanner-layer-left">
                            <p>Cosmetics Collection</p>
                            <a href="">Foundation and <br /> powder brush</a>
                            <br />
                            <button className="mt-5">Discover Now <i className="bx bx-chevron-right"></i></button>
                        </div>
                    </div>
                </div>
                <div className="lgbanner-col">
                    <div className="lgbanner-img">
                        <img src={img2} alt="" />
                        <div className="lgbanner-layer-right">
                            <div className="lgbanner-text">
                                <a className='under'>TOPS BLOUSE SHIRTS</a> <br />
                                <a href="">SHOP COLLECTION</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="timer-row">
                <div className="timer-col">
                    <p>Discover our Beauty Selection <span>75% Extra Discount for you</span></p>
                </div>
                <div className="timer-col">
                    <div className="timer">
                        <div className="time">
                            <b>{DaysTimer}</b>
                            <p>DAYS</p>
                        </div>
                        <div className="time">
                            <b>{HoursTimer}</b>
                            <p>HRS</p>
                        </div>
                        <div className="time">
                            <b>{MinutesTimer}</b>
                            <p>MIN</p>
                        </div>
                        <div className="time">
                            <b>{SecondsTimer}</b>
                            <p>SEC</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LgBanner;