import React from 'react'
import { useDispatch, useSelector } from 'react-redux';

const CartIsFull = ({ children }) => {
    const items = useSelector((state) => state.cart.items)

    if (items.length > 0) {
        return children;
    } else {
        return null;
    }
}

export const CartIsEmpty = ({ children }) => {
    const items = useSelector((state) => state.cart.items)

    if (items.length < 1) {
        return children;
    } else {
        return null;
    }
}

export default CartIsFull;