import React from 'react';
import './shopStyle.css';
import logo from '../../images/logo.svg';
import logo2 from '../../images/logo2.svg';
import cat from "../../images/smlCat.webp"
import cat2 from "../../images/smlcat2.webp"
import cat3 from "../../images/smlcat3.webp"
import cat4 from "../../images/smlcat4.webp"
import prod from "../../images/prod1.webp"
import brand1 from "../../images/brand1.webp"
import brand2 from "../../images/brand2.webp"
import brand3 from "../../images/brand3.webp"
import brand4 from "../../images/brand4.webp"
import brand5 from "../../images/brand5.webp"
import brand6 from "../../images/brand6.webp"
import cartImg from "../../images/empty-cart.webp"
import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux/es/hooks/useSelector';
import { useDispatch } from 'react-redux';
import { Add, addProd, remove } from '../../Redux/features/cartSlice';
import { Link } from 'react-router-dom';
import Footer from '../../Components/footer';
import { Products } from '../../Data/products';
import CartIsFull, { CartIsEmpty } from '../../Data/CartInfo';



const Shop = () => {
    const [menuClicked, setmenuClicked] = useState(false);
    const [dropdownClicked, setdropdownClicked] = useState(false);
    const [cartClicked, setcartClicked] = useState(false);
    const [scrolled, setscrolled] = useState(false);
    const [Layout, setLayout] = useState(false);
    const cart = useSelector(state => state.cart.items);
    const dispatch = useDispatch();


    const handleDropdown = () => {
        if (dropdownClicked == false) {
            setdropdownClicked(true);
        } else {
            setdropdownClicked(false);
        }
    }

    const handleCart = () => {
        if (cartClicked == false) {
            setcartClicked(true);
        } else {
            setcartClicked(false);
        }
    }


    useEffect(() => {
        const handleScroll = () => {
            if (window.screenY != "undefined") {
                return window.scrollY;
            }
        }

        window.addEventListener('scroll', () => {
            let scrolled = handleScroll();

            if (scrolled > 85) {
                setscrolled(true);
            }
            else {
                setscrolled(false);
            }
        })
    })

    let total = 0;

    cart.forEach(item => {
        total += item.totalPrice;
    });

    const handleLayout = () => {
        if (Layout == false) {
            setLayout(true);
        } else {
            setLayout(false);
        }
    }


    return (
        <div className="shop-section">
            <div className='cart-navbar'>
                <div className={scrolled ? "navbar-row active" : "navbar-row"}>
                    <div className="logo">
                        <div className="logo-img">
                            {scrolled ? <img src={logo2} alt="" /> : <img src={logo2} alt="" />}
                        </div>
                    </div>
                    <div className="navlinks">
                        <Link className='link' to={"/"}>Home </Link>
                        <Link className='link' to={"/Shop"}>Shop </Link>
                        <Link className='link' to={"/Cart"}>Cart</Link>
                        <Link className='link' to={"/Blog"}>Blog</Link>
                        <Link className='link' to={"/Contact"}>Contact</Link>
                    </div>
                    <div className="nav-icons">
                        <i className="bx bx-search"></i>
                        <i className="bx bx-heart"></i>
                        <i className="bx bx-shopping-bag cart-icon" onClick={() => { handleCart() }}>
                            <span className={scrolled ? 'cart-counter active' : 'cart-counter'}>{cart.length}</span>
                        </i>
                        <i className="bx bx-menu-alt-right side-menu" onClick={() => { setmenuClicked(true) }}>
                        </i>
                    </div>
                </div>
                <div className={menuClicked ? "sidebar active" : "sidebar"}>
                    <img src={logo2} className="mt-3 ms-3" />
                    <span className='close-btn' onClick={() => setmenuClicked(false)}>&times;</span>
                    <div className="sidebar-dropper" onClick={() => { handleDropdown() }}>
                        <div className="dropper-flex">
                            <p><i className="bx bx-menu mx-2"></i>All Categories</p>
                            <i className="bx bx-chevron-down"></i>
                        </div>
                        <div className={dropdownClicked ? "sidebar-dropdown show" : "sidebar-dropdown"}>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat} alt="" />
                                </div>
                                <p>Beauty of Skin</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat2} alt="" />
                                </div>
                                <p>Awesome Lip Care</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat3} alt="" />
                                </div>
                                <p>Facial Care</p>
                            </div>
                            <div className="dropdown-flex">
                                <div className="dropdown-img">
                                    <img src={cat4} alt="" />
                                </div>
                                <p>Discover SkinCare</p>
                            </div>
                        </div>
                    </div>
                    <div className="sidebar-links">
                        <p>Home</p>
                        <p>Products</p>
                        <p>eCommerce</p>
                        <p>More Pages</p>
                        <p>Coupons</p>
                        <p>Blog</p>
                        <p>Contact</p>
                    </div>
                    <button className="sidebar-btn">Contact Us</button>
                </div>

                {/***************Shopping Cart**************/}

                <div className={cartClicked ? "cart-section active" : "cart-section"}>
                    <div className="cart">
                        <div className="cart-heading">
                            <b>Shopping Cart</b>
                            <span onClick={() => handleCart()}>&times;</span>
                        </div>
                        <CartIsFull>
                            <div className="cart-body">
                                {
                                    cart.map((cart) => (
                                        <div key={cart.id} className="cart-flex">
                                            <div className="cart-img">
                                                <img src={cart.img} alt="" />
                                            </div>
                                            <div className="cart-title">
                                                <b>{cart.title}</b>
                                                <p>{cart.price}</p>
                                                <div className="quantity-flex">
                                                    <i className="bx bx-minus border-end" onClick={() => { dispatch(remove(cart.id)) }}></i>
                                                    <input type="text" value={cart.quantity} name="" id="" />
                                                    <i className="bx bx-plus border-start" onClick={() => { dispatch(Add(cart)) }}></i>
                                                </div>
                                                <span onClick={() => { dispatch(remove(cart.id)) }}>&times;</span>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        </CartIsFull>
                        <CartIsFull>
                            <div className="cart-footer">
                                <div className="cart-flex">
                                    <b>Total</b>
                                    <b>${total}</b>
                                </div>
                                <div className="cart-buttons">
                                    <Link to={'/Cart'} className='btn cart-btn py-3'>View Cart</Link>
                                    <button className='btn cart-btn'>Checkout</button>
                                </div>
                            </div>
                        </CartIsFull>
                        <CartIsEmpty>
                            <div className="cart-alt">
                                <div className="cart-alt-img">
                                    <img src={cartImg} alt="" />
                                </div>
                                <div className="cart-alt-text">
                                    <h3 className='mb-5'>Your Cart is Empty</h3>
                                    <button className='btn btn-dark'><a href="/Shop" style={{ color: "white", textDecoration: 'none' }}>Go To Shop</a></button>
                                </div>
                            </div>
                        </CartIsEmpty>

                    </div>
                </div>
            </div>
            {/***************************************************/}
            <div className="cart-lg-heading mx-5 my-5">
                <h2>Shop Grid</h2>
                <div className="cart-lg-text d-flex">
                    <p className='mx-2'>Home</p>
                    /
                    <p className='mx-2'>Shop Grid</p>
                </div>
            </div>
            {/***************************************************/}

            <div className="shop-row">
                <div className="shop-col shop-left">
                    <b>Price Filter</b>
                    <br />
                    <input type="range" name="" id="" />
                    <div className="shop-col-flex">
                        <p>$0 - $1199</p>
                        <p>Filter</p>
                    </div>
                    <br />
                    <b>Product Status</b>
                    <br />
                    <input type="checkbox" name="" id="" /> On sale
                    <br />
                    <input type="checkbox" name="" id="" /> On sale
                    <br />
                    <b className='py-5'>Categories</b>
                    <br />
                    <div className="categories-list py-3 border-top">
                        <div className="cat-list">
                            <div className="cat-flex">
                                <p>Beauty of Skin</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Bags</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Awesome Lip Stick</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Mobile Tablets</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Clothing</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Bluetooth</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Facial Care</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Shoes</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>CPU Heat Pipes</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Headphones</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Discover Skincare</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Bracelets</p>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <p>Smart Watch</p>
                                <p>2</p>
                            </div>
                        </div>
                    </div>
                    <b>Filter by Color</b>
                    <br />
                    <div className="categories-list  py-4 border-top">
                        <div className="color-list">
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p> Burning Sand</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Antique Brass</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Pinkish Tan</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Purply Blue</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Light Grey</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Baby Pink</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Iridium</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Bluish Cyan</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Red</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Orange</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Faded Green</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Dark Green</p>
                                </div>
                                <p>2</p>
                            </div>
                            <div className="cat-flex">
                                <div className="color-flex">
                                    <div className="color"></div>
                                    <p>Brick Red</p>
                                </div>
                                <p>2</p>
                            </div>
                        </div>
                    </div>
                    <b>Top Rated Products</b>
                    <div className="sml-side-row border-top py-4">
                        {
                            Products.slice(13, 16).map((prod) => (
                                <div className="sml-side-col">
                                    <div className="sml-side-img">
                                        <img src={prod.img} alt="" />
                                    </div>
                                    <div className="sml-side-content">
                                        <div className="rating">
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                        </div>
                                        <div className="sml-side-title">
                                            <h3>{prod.title} </h3>
                                        </div>
                                        <p>$ {prod.price}</p>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                    <b>Popular Brands</b>
                    <br />
                    <div className="sml-brand-row border-top py-4">
                        <div className="sml-brand-col">
                            <img src={brand1} alt="" />
                        </div>
                        <div className="sml-brand-col">
                            <img src={brand2} alt="" />
                        </div>
                        <div className="sml-brand-col">
                            <img src={brand3} alt="" />
                        </div>
                        <div className="sml-brand-col">
                            <img src={brand4} alt="" />
                        </div>
                        <div className="sml-brand-col">
                            <img src={brand5} alt="" />
                        </div>
                        <div className="sml-brand-col">
                            <img src={brand6} alt="" />
                        </div>
                    </div>
                    <br />
                    <b>Reset Filter</b>
                    <br />
                    <div className=" border-top py-4">
                        <button className='shop-button'>Reset Filter</button>
                    </div>
                </div>


                <div className="shop-col">
                    <div className="shop-heading my-3">
                        <div className="shop-heading-flex">
                            <div className="shop-heading-icons">
                                <i onClick={() => setLayout(false)} className={!Layout ? "bx bx-grid-alt active" : "bx bx-grid-alt text-muted"}></i>
                                <i onClick={() => setLayout(true)} className={Layout ? "bx bx-menu active" : "bx bx-menu text-muted"}></i>
                            </div>
                            <div className="shop-heading-p">
                                Showing 1 - 12 of 37 results
                            </div>
                        </div>
                        <div className="shop-heading-flex">
                            <div className="shop-heading-select">
                                <select name="" value="">
                                    <option value="Default Sorting">Default Sorting</option>
                                    <option value="">Low To High</option>
                                    <option value="">High To Low</option>
                                    <option value="">New Added</option>
                                    <option value="">On Sale</option>
                                </select>
                            </div>
                            <div className="shop-heading-button">
                                <button className='shop-btn'><i className="bx bx-filter"></i>Filter</button>
                            </div>
                        </div>
                    </div>
                    <div className="shop-products-row">
                        {
                            Products.slice(4, 16).map((prod) => (
                                <div key={prod.id} className={Layout ? "shop-products-col active" : "shop-products-col"} onClick={() => { addProd(prod) }}>
                                    <div className={Layout ? "shop-products-img active" : "shop-products-img"}>
                                        <img src={prod.img} alt="" />
                                        <div className="shop-products-layer">
                                            <i className="bx bx-shopping-bag" onClick={() => { dispatch(Add(prod)) }}></i>
                                            <i className="bx bx-heart"></i>
                                            <i className="bx bx-show"></i>
                                        </div>
                                    </div>
                                    <div className="shop-products-content my-2">
                                        <small className='text-muted'>{prod.type} </small>
                                        <h3 className=''>{prod.title} </h3>
                                        <div className="prod-rating">
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                            <i className="bx bxs-star"></i>
                                        </div>
                                        <p className={Layout ? 'shop-products-desc active' : 'shop-products-desc'}>Achieve that sun-kissed glow with the Baked Mineral Bronzer
                                            from INIKA. Perfect for contouring, the</p>
                                        <div className="shop-products-price">
                                            <b>$ 60.00</b>
                                            <p className='text-muted pt-3'>$ 57.00</p>
                                        </div>
                                        <button className={Layout ? "col-btn active" : "col-btn"}>Add To Cart</button>
                                    </div>
                                </div>
                            ))
                        }
                    </div>

                    <div className="pagination">
                        <div className="page active">1</div>
                        <div className="page">2</div>
                        <div className="page">3</div>
                        <div className="page">4</div>
                        <div className="page"><i className="bx bx-right-arrow-alt"></i></div>
                    </div>
                </div>
            </div>


            {/***************************************************/}
            <Footer />
        </div>
    )
}

export default Shop;